<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <polygon :points="computedPoints"
                    :stroke="polygonConfig.stroke"
                    :stroke-width="polygonConfig.strokeWidth"
                    :fill="polygonConfig.fill"
                    :fill-rule = "polygonConfig.fillRule"></polygon>
          </svg>
        </div>

        <div class="svg-config-form__wrapper">
          <el-form ref="polygonConfigForm" :model="polygonConfig" label-width="200px">
            <el-form-item label="随机点">
                <el-checkbox v-model="showRandomPoint"/>
            </el-form-item>
            <el-form-item label="点">
              <el-checkbox-group v-model="checkedPoints">
                <el-checkbox v-for="point in points" :key="point" :label="point"/>
              </el-checkbox-group>
            </el-form-item>

            <el-form-item label="笔划颜色(stroke)">
              <el-color-picker v-model="polygonConfig.stroke" show-alpha >
              </el-color-picker>
              {{polygonConfig.stroke}}
            </el-form-item>

            <el-form-item label="笔划宽度(strokeWidth)">
              <el-slider
                  v-model="polygonConfig.strokeWidth"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="填充颜色(fill)">
              <el-color-picker v-model="polygonConfig.fill" show-alpha >
              </el-color-picker>
              {{polygonConfig.fill}}
            </el-form-item>

            <el-form-item label="填充规则(fillRule)">
              <el-select v-model="polygonConfig.fillRule" clearable placeholder="请选择">
                <el-option label="nonzero" value="nonzero"/>
                <el-option label="evenodd" value="evenodd"/>
                <el-option label="inherit" value=""/>
              </el-select>
            </el-form-item>

          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import DemoPageMixin from '@/mixins/demo-page-mixin'
import { getRandomInt } from '@/utils'
export default {
  name: 'SVGDemo005',
  mixins: [DemoPageMixin],
  data () {
    return {
      timestamp: null,
      showRandomPoint: false,
      points: ['100,10', '40,180', '190,60', '10,60', '160,180'],
      checkedPoints: ['100,10', '40,180', '190,60', '10,60', '160,180'],
      polygonConfig: {
        stroke: 'rgba(0, 0, 0, 0.8)',
        strokeWidth: 2,
        fill: 'rgba(255, 69, 0, 0.68)',
        fillRule: 'nozero'
      }
    }
  },
  created () {
    this.addIntervalHandler(() => {
      this.timestamp = new Date()
    }, 1500)
  },
  methods: {
  },
  computed: {
    computedPoints () {
      if (this.showRandomPoint && this.timestamp) {
        let cnt = getRandomInt(3, 10)
        let points = []
        for (let i = 0; i < cnt; i++) {
          const x = getRandomInt(10, 290)
          const y = getRandomInt(10, 290)
          points.push(`${x},${y}`)
        }
        return points.join(' ')
      }
      return this.checkedPoints.join(' ')
    }
  }
}
</script>

<style scoped>

